<template>
    <div>
        <div class="text-center mt-2 mb-4">
           <span class="main-color" style="font-size: 20px">ກະລຸນາເລືອກປະເພດວຽກທີ່ຕ້ອງການ</span>
        </div>
        <app-category :categories="categories"></app-category>
    </div>
</template>

<script>
    import appCategory from '../../components/category/Category'
    export default {
        components: {
          appCategory
        },
        data: () => ({
            categories: []
        }),
        methods: {
          async fetchCategories () {
              const results = await this.$axios.get('category/getall')
              this.categories = results.data.categories
           }
        },
        created () {
            this.fetchCategories()
        }
    }
</script>
